import React from 'react';
import ReactDOM from 'react-dom';
import "xss"
import "./css/style.css"


class Nav extends React.Component {
  dom() {
    return (
      <header className="nav-header">
        <nav className="nav">
          <ul>
            <li><a href="https://www.google.com">Home</a></li>
            <li><a href="#test">Test</a></li>
            <li><a href="#about">About</a></li>
          </ul>
        </nav>
      </header>
    )
  }
  render() {
    return this.dom()
  }
}

function Clicker() {
  const [counts, setCounts] = React.useState(0);
  if(counts != localStorage.getItem("buttonClicks")) {
    if(localStorage.getItem("buttonClicks") === null) {
      localStorage.setItem("buttonClicks",0)
    }
    setCounts(localStorage.getItem("buttonClicks"))
  }
  console.log(counts)
  return (
    <div className="clicker">
      <p> You Clicked the button <code>{counts}</code> Times!</p>
      <button onClick={() => {
        localStorage.setItem("buttonClicks",+localStorage.getItem("buttonClicks") + 1)
        setCounts(localStorage.getItem("buttonClicks"))
      }}>Click me</button>
    </div>
  )
}

function App() {
  return (
    <div id="hello">
      <Nav/>
      <Clicker/>
      <footer className="foot">
        <ul>
          <li><code className="copyright">&#169; 2020</code></li>
        </ul>
      </footer>
    </div>
  )
}

function render(something) {
  ReactDOM.render(
    something,
    document.getElementsByClassName("main")[0]
  )
}

render(App());